import { useCallback } from 'react'
import { createClient } from '@/lib/supabase/client'
import { toast } from 'sonner'
import { logger } from '@/lib/logger'

export interface ConfirmDateActionConfig {
  field: 'statement_received' | 'actual_received'
  confirmMessage: {
    confirm: string
    cancel: string
  }
  successMessage: {
    confirm: string
    cancel: string
  }
  completedText: string
  waitingText: string
}

export interface UseConfirmDateActionProps {
  config: ConfirmDateActionConfig
  currentUserName: string | null
  canPerformAction: boolean
  onUpdate?: () => void
  onOptimisticUpdate?: (params: {
    itemId: number
    selectedDate?: Date
    action: 'confirm' | 'cancel'
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
    }
  }) => void
}

export function useConfirmDateAction({
  config,
  currentUserName,
  canPerformAction,
  onUpdate,
  onOptimisticUpdate
}: UseConfirmDateActionProps) {
  const supabase = createClient()

  const handleConfirm = useCallback(async (
    itemId: number | string,
    selectedDate: Date,
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
    }
  ) => {
    
    if (!canPerformAction) {
      console.log(`‚ùå Í∂åÌïú ÏóÜÏùå`, { canPerformAction, currentUserName })
      logger.warn(`‚ùå Í∂åÌïú ÏóÜÏùå`, { canPerformAction })
      toast.error(`${config.field === 'statement_received' ? 'Í±∞ÎûòÎ™ÖÏÑ∏ÏÑú' : 'ÏûÖÍ≥†'} ÌôïÏù∏ Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.`)
      return
    }

    const itemIdStr = String(itemId)
    const numericId = typeof itemId === 'number' ? itemId : Number(itemId)

    if (Number.isNaN(numericId)) {
      logger.error('‚ùå ÏûòÎ™ªÎêú ID', { itemId, numericId })
      toast.error('Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ìï≠Î™© ID ÏûÖÎãàÎã§.')
      return
    }

    // ÌôïÏù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÌëúÏãú
    if (itemInfo) {
      const confirmMessage = `ÌíàÎ™©Î™Ö: ${itemInfo.item_name || '-'}
Í∑úÍ≤©: ${itemInfo.specification || '-'}
ÏàòÎüâ: ${itemInfo.quantity?.toLocaleString() || 0}
Îã®Í∞Ä: ‚Ç©${itemInfo.unit_price_value?.toLocaleString() || 0}
Ìï©Í≥Ñ: ‚Ç©${itemInfo.amount_value?.toLocaleString() || 0}
ÎπÑÍ≥†: ${itemInfo.remark || '-'}

${config.confirmMessage.confirm}`
      
      if (!window.confirm(confirmMessage)) {
        return
      }
    }

    try {
      let updateData: any

      if (config.field === 'statement_received') {
        updateData = {
          is_statement_received: true,
          statement_received_date: selectedDate.toISOString(),
          statement_received_by_name: currentUserName
        }
      } else if (config.field === 'actual_received') {
        updateData = {
          actual_received_date: selectedDate.toISOString()
        }
      }

      console.log('üìù ÏóÖÎç∞Ïù¥Ìä∏Ìï† Îç∞Ïù¥ÌÑ∞:', updateData)

      const { data, error } = await supabase
        .from('purchase_request_items')
        .update(updateData)
        .eq('id', numericId)
        .select()

      console.log('üìù DB ÏóÖÎç∞Ïù¥Ìä∏ Í≤∞Í≥º:', { data, error })

      if (error) {
        console.error('‚ùå DB ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®:', error)
        logger.error('‚ùå DB ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®', error)
        throw error
      }

      console.log('‚úÖ DB ÏóÖÎç∞Ïù¥Ìä∏ ÏÑ±Í≥µ:', data)
      logger.info('‚úÖ DB ÏóÖÎç∞Ïù¥Ìä∏ ÏÑ±Í≥µ')

      if (onOptimisticUpdate) {
        onOptimisticUpdate({
          itemId: numericId,
          selectedDate,
          action: 'confirm',
          itemInfo
        })
      }

      // Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ®ÏùÑ ÏúÑÌï¥ onUpdate Ìò∏Ï∂ú
      if (onUpdate) {
        onUpdate()
      }
      
      toast.success(config.successMessage.confirm)
    } catch (error) {
      logger.error('‚ùå Ï†ÑÏ≤¥ Ï≤òÎ¶¨ Ïã§Ìå®', error)
      toast.error(`${config.field === 'statement_received' ? 'Í±∞ÎûòÎ™ÖÏÑ∏ÏÑú' : 'ÏûÖÍ≥†'} ÌôïÏù∏ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.`)
    }
  }, [config, currentUserName, canPerformAction, onUpdate, onOptimisticUpdate, supabase])

  const handleCancel = useCallback(async (
    itemId: number | string,
    itemInfo?: {
      item_name?: string
      specification?: string
      quantity?: number
      unit_price_value?: number
      amount_value?: number
      remark?: string
    }
  ) => {
    if (!canPerformAction) {
      console.log(`‚ùå Ï∑®ÏÜå Í∂åÌïú ÏóÜÏùå`, { canPerformAction, currentUserName })
      toast.error(`${config.field === 'statement_received' ? 'Í±∞ÎûòÎ™ÖÏÑ∏ÏÑú' : 'ÏûÖÍ≥†'} ÌôïÏù∏ Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.`)
      return
    }

    const itemIdStr = String(itemId)
    const numericId = typeof itemId === 'number' ? itemId : Number(itemId)

    if (Number.isNaN(numericId)) {
      toast.error('Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ Ìï≠Î™© ID ÏûÖÎãàÎã§.')
      return
    }

    // ÌôïÏù∏ Îã§Ïù¥ÏñºÎ°úÍ∑∏ ÌëúÏãú
    if (itemInfo) {
      const confirmMessage = `ÌíàÎ™©Î™Ö: ${itemInfo.item_name || '-'}
Í∑úÍ≤©: ${itemInfo.specification || '-'}
ÏàòÎüâ: ${itemInfo.quantity?.toLocaleString() || 0}
Îã®Í∞Ä: ‚Ç©${itemInfo.unit_price_value?.toLocaleString() || 0}
Ìï©Í≥Ñ: ‚Ç©${itemInfo.amount_value?.toLocaleString() || 0}
ÎπÑÍ≥†: ${itemInfo.remark || '-'}

${config.confirmMessage.cancel}`
      
      if (!window.confirm(confirmMessage)) {
        return
      }
    }

    try {
      console.log(`üîÑ ${config.field} ÌôïÏù∏ Ï∑®ÏÜå ÏãúÏûë`, { 
        itemId, 
        itemName: itemInfo?.item_name 
      })

      let updateData: any

      if (config.field === 'statement_received') {
        updateData = {
          is_statement_received: false,
          statement_received_date: null,
          statement_received_by_name: null
        }
      } else if (config.field === 'actual_received') {
        updateData = {
          actual_received_date: null
        }
      }

      console.log('üîÑ Ï∑®ÏÜå ÏóÖÎç∞Ïù¥Ìä∏Ìï† Îç∞Ïù¥ÌÑ∞:', updateData)

      const { data, error } = await supabase
        .from('purchase_request_items')
        .update(updateData)
        .eq('id', numericId)
        .select()

      console.log('üîÑ Ï∑®ÏÜå DB ÏóÖÎç∞Ïù¥Ìä∏ Í≤∞Í≥º:', { data, error })

      if (error) {
        console.error('‚ùå Ï∑®ÏÜå DB ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®:', error)
        logger.error('‚ùå DB ÏóÖÎç∞Ïù¥Ìä∏ Ïã§Ìå®', error)
        throw error
      }

      console.log(`‚úÖ ${config.field} ÌôïÏù∏ Ï∑®ÏÜå ÏÑ±Í≥µ:`, data)
      logger.info(`‚úÖ ${config.field} ÌôïÏù∏ Ï∑®ÏÜå ÏÑ±Í≥µ`)

      if (onOptimisticUpdate) {
        onOptimisticUpdate({
          itemId: numericId,
          action: 'cancel',
          itemInfo
        })
      }

      // Í∞ïÏ†ú ÏÉàÎ°úÍ≥†Ïπ®ÏùÑ ÏúÑÌï¥ onUpdate Ìò∏Ï∂ú
      if (onUpdate) {
        onUpdate()
      }
      
      toast.success(config.successMessage.cancel)
    } catch (error) {
      logger.error(`‚ùå ${config.field} ÌôïÏù∏ Ï∑®ÏÜå Ïã§Ìå®`, error)
      toast.error(`${config.field === 'statement_received' ? 'Í±∞ÎûòÎ™ÖÏÑ∏ÏÑú' : 'ÏûÖÍ≥†'} ÌôïÏù∏ Ï∑®ÏÜå Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.`)
    }
  }, [config, canPerformAction, onUpdate, onOptimisticUpdate, supabase])

  const isCompleted = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.is_statement_received
    } else if (config.field === 'actual_received') {
      return !!item.actual_received_date
    }
    return false
  }, [config.field])

  const getCompletedDate = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.statement_received_date
    } else if (config.field === 'actual_received') {
      return item.actual_received_date
    }
    return null
  }, [config.field])

  const getCompletedByName = useCallback((item: any) => {
    if (config.field === 'statement_received') {
      return item.statement_received_by_name
    } else if (config.field === 'actual_received') {
      // ÏûÖÍ≥†ÏôÑÎ£åÎäî Ï≤òÎ¶¨Ïûê Ï†ïÎ≥¥Î•º Í∏∞Î°ùÌïòÏßÄ ÏïäÏùå
      return null
    }
    return null
  }, [config.field])

  return {
    config,
    handleConfirm,
    handleCancel,
    isCompleted,
    getCompletedDate,
    getCompletedByName
  }
}